    /**
     * @description  物流公司列表获取
    */
    personalApi.getExpressList().then(res => {
      // 数据转换
      res.data.data.forEach(item => {
        this.array.push(item.expressName)
      })
      this.arrayStart = res.data.data
    })
    if (this.$route.query.repairSn) {
      this.repairSn = this.$util.strJSONParse(this.$route.query.repairSn, '') // 逆向号+网单号
    }
