     * @description  产品信息获取
    */
    personalApi.getOrderRepairsInfo({ orderProductId: parseInt(this.repairSn.orderProductId) }).then(res => {
      this.list = res.data.data
    })
  },
  data() {
    return {
      loading: false,
      arrayStart: [], // 物流公司接口返回数据
      showLogistics: false, // 弹窗物流公司
      list: {
        productDefaultImageUrl: '@/assets/imgs/pc/camera.png',
        productFullName: '商品名称',
        productAttrName: '规格名称'
      },
      ifChange: true,
      index: null,
      array: [
