    },
    /**
     * @description  提交
    */
    submission() {
      this.$toast.loading({
        forbidClick: true,
        message: '加载中...',
        duration: 0
      })
      // 校验
      const myreg = /^[1][3,4,5,7,8][0-9]{9}$/
      if (this.index === null) {
        this.$toast.fail('请选择物流公司')
        return false
      }
      if (this.value === '') {
        this.$toast.fail('请填写物流单号')
        return false
      }
      if (this.phoneValue === '') {
        this.$toast.fail('请填写联系电话')
        return false
      }
      if (this.phoneValue && myreg.test(this.phoneValue)) {

      } else {
        this.$toast.fail('请填写正确联系电话')
        return false
      }
      // picUrl图片路径处理
      var context = this
      // 图片存在
      if (context.imgs.length > 0) {
        context.loading = true
        personalApi.uploadPictureBybase64({
          base64s: context.imgs.map(img => img.content.split(',')[1])
        }).then(imgRes => {
          context.loading = false
          if (imgRes.data.success) {
            const data = {
              repairSn: context.repairSn.repairSn, // 逆向单号
              expressCompany: context.arrayStart[context.index].expressName, // 快递公司
              expressNumber: context.value, // 快递单号
              expressCompanyCode: context.arrayStart[context.index].expressCode, // 快递公司码
              picUrl: imgRes.data.data.fileList.map(img => img.imgUrl).join(), // 上传图片
              description: context.description // 说明
            }
            personalApi.requestOrderRepairExpress(data).then(res => {
              if (res.data.success) {
                context.$toast.clear()
                context.$router.replace({ path: '/details', query: { orderProductId: context.repairSn.orderProductId } })
                context.$router.go(-1)
              } else {
                context.$toast.fail(res.data.message)
              }
            })
          } else {
            context.$toast(imgRes.data.message)
          }
        }).catch(() => {
          this.loading = false
          context.$toast.fail('上传图片失败')
        })
      } else {
        // 无图片上传
        const data = {
          repairSn: context.repairSn.repairSn, // 逆向单号
          expressCompany: context.arrayStart[context.index].expressName, // 快递公司
          expressNumber: context.value, // 快递单号
          expressCompanyCode: context.arrayStart[context.index].expressCode, // 快递公司码
          description: context.description // 说明
        }
        personalApi.requestOrderRepairExpress(data).then(res => {
          if (res.data.success) {
            context.$toast.clear()
            context.$router.replace({ path: '/details', query: { orderProductId: context.repairSn.orderProductId } })
            context.$router.go(-1)
          } else {
            context.$toast.fail(res.data.message)
          }
        })
      }
    },
